
import { Component, Vue } from "vue-property-decorator";
import { getPhotos } from "@/api/api";
import { IPhoto } from "@/types";

@Component({
  components: {},
})
export default class extends Vue {
  photos: IPhoto[] = [];
  async mounted() {
    try {
      const response = await getPhotos();
      const data = response.data;
      if (data.length) {
        this.photos = data;
      }
    } catch (error) {
      console.error(error);
    }
  }
}
